import React, {useState, useEffect} from 'react';
import './App.scss';
import axios from 'axios';

function App() {
  const [spaceX, setData] = useState([]);
  const [launchYear, setlaunchYear] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isError, setIsError] = useState(false);
 
  useEffect(() => {
    const fetchData = async () => {
      setIsError(false);
      setIsLoading(true);
      try{
      const result = await axios(
        'https://api.spacexdata.com/v3/launches?limit=100',
      );
      setData(result.data);
      }catch(error){
        setIsError(true)
      }
      setIsLoading(false);
    };
    fetchData();
  },[]);

  useEffect(()=>{
    const fetchData = async () => {
      const result = await axios(
        'https://api.spacexdata.com/v3/launches?limit=100',
      );
     
      console.log(result)
      const filtered = result.data.filter(p => (
         p.launch_year.includes(launchYear)
      ));
      console.log(filtered)
      setData(filtered);
    };
    fetchData();
    
  },[launchYear])

  return (
    <>
    <div className="App">
      <header className="App-header">
        <div className="container">
          <h3>SpaceX Launch Programs</h3>
          <div className="wrapper">
            <div className="left-sidebar">
              <div className="filter-con">
                <h2>Filters</h2>
                <div className="inner-filterbox">
                  <h4>Launch year</h4>
                  <div className="filterholder">
                    <button className="a" 
                      active={launchYear ==="2006"}
                      onClick={() => setlaunchYear("2006")}
                    >2006</button>
                    <button
                      active={launchYear==="2007"}
                      onClick={() => setlaunchYear("2007")}
                    >2007</button>
                    <button
                      active={launchYear==="2008"}
                      onClick={() => setlaunchYear("2008")}
                    >2008</button>
                    <button
                      active={launchYear==="2009"}
                      onClick={() => setlaunchYear("2009")}
                    >2009</button>
                    <button
                      active={launchYear==="2010"}
                      onClick={() => setlaunchYear("2010")}
                    >2010</button>
                    <button
                      active={launchYear==="2011"}
                      onClick={() => setlaunchYear("2011")}
                    >2011</button>
                    <button
                      active={launchYear==="2012"}
                      onClick={() => setlaunchYear("2012")}
                    >2012</button>
                  </div>
                </div>
                <div className="inner-filterbox">
                  <h4>Successful Launch</h4>
                  <div className="filterholder">
                    <button>true</button>
                    <button>false</button>
                  </div>
                </div>
                <div className="inner-filterbox">
                  <h4>Successful Landing</h4>
                  <div className="filterholder">
                    <button>true</button>
                    <button>false</button>
                  </div>
                </div>
              </div>
            </div>
            <div className="main-area">
            {isError && <div>Something went wrong ...</div>}
            {isLoading ? (<div>Loading ...</div>) :(
            <>  
              {spaceX.map((item,i)=>(
                  <div className="boxWrapper" key={item.flight_number}>
                    <div className="imgCon">
                      <img className="img-responsive" src={item.links.mission_patch_small} alt="#"/>
                    </div>
                    <div className="title"><strong>{item.mission_name}#{item.flight_number}</strong></div>
                    <ul className="boxContent">
                      <li className="itemlist"><span><strong>Mission Ids:</strong></span> 
                        {
                            item.mission_id.map((m_id,i)=>(<span key={i.toString()}>{m_id}</span>))
                        }
                      </li>
                      <li className="itemlist"><span><strong>Launch Year:</strong></span> <span>{item.launch_year}</span></li>
                      <li className="itemlist"><span><strong>Successful Launch:</strong></span> <span>{item.launch_success}</span></li>
                      <li className="itemlist"><span><strong>Successful Landing:</strong></span> <span>{item.launch_date_unix}</span></li>
                    </ul>

                  </div>
              ))}
            </>
            )}
            </div>
          </div>
        </div>
      </header>
    </div>
    </>
  );
}

export default App;
